@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
  --main-black: #1d1d1d;
}

/* base styles & title */
body {
  font-family: "Noto Serif";
  color: #4e4e4e;
  color: var(--secondary);
}

.App {
  max-width: 960px;
  margin: 0 auto;
}

.title h1 {
  color: #efb6b2;
  color: var(--primary);
  font-size: 1rem;
  letter-spacing: .2rem;
  font-weight: normal;
  overflow: hidden;
}

.title h2,
.title p {
  text-align: center;
}

.title h2 {
  font-size: 1.6rem;
  overflow: hidden;
}

strong {
  color: #1d1d1d;
  color: var(--main-black)
}

/**media**/

@media screen and (min-width: 768px) {
  .App {
    padding: 1rem 2rem;
  }

  .title h1 {
    font-size: 1.2rem;
    letter-spacing: .3rem;
  }

  .title h2 {
    margin-top: 3rem;
    font-size: 2.6rem;
  }

  .title p {
    font-size: 1.4rem;
    margin: 0;
  }
}

