/* image grid styles */

.ImageLayout_imgGrid__CTAEi {
  margin: 2rem auto;
  display: grid;
  grid-template-columns: 1fr;
}



.ImageLayout_imgWrap__3fJhO {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
  margin-bottom: 1.5rem;
  cursor: pointer;
}


.ImageLayout_imgWrap__3fJhO img {
  width: 100%;
  height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
  margin-bottom: 1rem;
}


/*** media ***/

@media screen and (min-width: 768px) {
  .ImageLayout_imgGrid__CTAEi {
    margin: 2rem auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1.5rem;
  }

}
.ModalWindow_backdrop__xtyMn {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}


.ModalWindow_backdrop__xtyMn img {
  display: block;
  max-width: 100%;
